body {
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  max-width: 1200px;
  padding: 0 30px;
}

h1 {
  text-align: center;
  text-transform: uppercase;
}

.messageWrapper {
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  padding: 20px;
  width: fit-content;
}

.btnWrapper {
  text-align: right;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modalContent {
  width: 500px;
  background-color: #fff;
  padding: 30px;
}

.header {
  margin-bottom: 7px;
  font-weight: 600;
}

.close {
  text-align: right;
  font-size: 1.5rem;
  margin-bottom: 20px;
}

form {
  display: flex;
  align-items: center;
}

label input {
  padding: 7px;
  margin-right: 10px;
}

.submitBtn {
  background-color: rgb(229, 218, 240);
  border: none;
  padding: 10px;
  border-radius: 5px;
}